import React,{useState,useEffect} from 'react'
import logo from '../../../Image/louis-hansel-shotsoflouis-d0UEoXDvJ5I-unsplash.jpg'
import logos from '../../../Image/micheile-henderson-vElbClo2M5g-unsplash.jpg'
import './HomeElements.css'
function Image(){
    const [width]=Windowwidth()
    const Small=()=>{
        return(
            <ul>
                <li><img src={logo}/></li>
            
            </ul>
        )
    }
    const Large=()=>{
        return(
            <ul>
                <li><img src={logo}/></li>
                <li><img src={logos}/></li>

            </ul>
        )
    }
    return(
        <>
           {width>960 ?  <Large />:<Small />} 
            
        </>
    )
}
function Windowwidth(){
    const [width,setWidth]=useState([window.innerWidth])
    useEffect(()=>{
        const eventHandler=()=>{
            setWidth([window.innerWidth])
        }
        window.addEventListener('resize',eventHandler)
    },[])

    return width;
    

}
function Applyimage(){

}





export default Image
