import React,{useState} from 'react'
import {Link} from 'react-router-dom'
import Button from '../Button/index'
import './NavbarElements.css'

function Navbar() {
    const [click,setClick]=useState(false)
    const handleClick=()=>setClick(!click)
    const closeMobileMenu=()=>setClick(false)
    return (
        <>
           <nav className='navbar'>
               <Link to='/' className='navbar_logo'>
                   Food Station
               </Link>
                <div className='menu-icon' onClick={handleClick} >
                    <i className={click ? 'fas fa-times':'fas fa-bars'}/>
                </div>
               <ul className={click ? 'nav_menu active':'nav_menu'}>
                   <li className='nav_items'>
                       <Link to='/' className='nav_links' onClick={closeMobileMenu}>
                           Home
                       </Link>
                   </li>
                   <li className='nav_items'>
                       <Link to='/services' className='nav_links' onClick={closeMobileMenu}>
                           Services
                       </Link>
                   </li>
                   <li className='nav_items'>
                       <Link to='/contact-us' className='nav_links' onClick={closeMobileMenu}>
                           Contact Us
                       </Link>
                   </li>
                   <li className='nav_items'>
                       <Link to='/menu' className='nav_links' onClick={closeMobileMenu}>
                           Menu
                       </Link>
                   </li>
                   <li className='nav_items'>
                       <Link to='/sign-up' className='nav_links_mobile' onClick={closeMobileMenu}>
                           Sign Up
                       </Link>
                       <Button/>
                   </li>

                   

               </ul>

           </nav>
        </>
    )
}

export default Navbar
